import Overlay from './overlay';
import Push from './push';
import Revolving from './reveal';
export var selectorPrefix = 'adhere-ui-slidelayout';
/**
 * slider
 * @param el
 * @param x
 * @param y
 * @param z
 * @param time
 * @param callback
 */
export function slider(el, x, y, z, time, callback) {
    if (time === void 0) { time = '0'; }
    if (callback) {
        callback(el);
    }
    // @ts-ignore
    el.style.transform = el.style.webkitTransform = "translate3d(" + x + "," + y + "," + z + ")";
    // @ts-ignore
    el.style.transition = el.style.webkitTransition = "all " + time + " ease";
}
/**
 * createMask
 * @param zIndex
 * @param closeCallback
 */
export function createMask(zIndex, closeCallback) {
    var el = document.createElement('div');
    el.innerHTML = "<div class='" + selectorPrefix + "-mask'></div>";
    var maskEl = el.firstElementChild;
    // @ts-ignore
    maskEl.style.zIndex = zIndex - 1;
    // @ts-ignore
    maskEl.addEventListener('click', function () {
        closeCallback();
    });
    return maskEl;
}
export default {
    Overlay: Overlay,
    Push: Push,
    Revolving: Revolving,
};
